@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.Image-wrapper {
  padding: 10px 12px;
}

.Image {
  background-position: center center;
  background-size: cover;
  border-radius: 50%;
  position: relative;
  height: 32px;
  width: 32px;
}

.Badge-wrapper {
  padding: 21px 12px;
}

.Badge {
  border-radius: 50%;
  height: 8px;
  width: 8px;
}

.Badge-inside-image {
  border: 2px solid white;
  position: absolute;
  right: -2px;
  top: 20px;
}

.Labels-wrapper {
  overflow: hidden;
  padding-right: 12px;
}

.Label {
  color: #222222;
  font-family: 'Open Sans', sans-serif;
  font-size: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Title {
  font-weight: bold;
  margin-bottom: 2px;
}

.Card-wrapper {
  align-items: center;
  border: 1px solid #dadada;
  border-radius: 4px;
  display: grid;
  height: 50px;
  min-width: 100px;
  max-width: 178px;
}

.Card-wrapper-with-image {
  grid-template-columns: 56px auto;
}

.Card-wrapper-without-image {
  grid-template-columns: 32px auto;
}

